import React, { Component } from 'react'
import Signup from './signup'
import {
    BrowserRouter as Router,
    Route,
    Link
} from 'react-router-dom'
import  Myroute1  from "./signup"

export const Upsign = () => {
    return <Myroute1 />
}

export const Myroute = () => {
    return (
        <Router>
            <div>
                <Route exact path="/signin" component={Signin} />
                <Route path="/signup" component={Upsign} />
            </div>
        </Router>
        

    )
}

class Signin extends Component {

    render() {
        return (
            // <Signup />
            // <Upsign/>
            <div>
                <div className="container subDiv">
                    <div className="d1"></div>
                    <div className="divs">
                        <div className="div1">
                            <img src={require("../img/1.png")} class="logo1" />

                        </div>
                        <div className="div2">
                            <h1 className="h11" >Sign in</h1>
                            <div className="d2">
                                <i className="fas fa-envelope-open icon1 d3" ></i>
                                <input type="email" class="inp" id="emailid" placeholder="Email" />
                                <p className="abc" id="a11">Please fill the input correctly*</p>

                            </div>
                            <div className="d4">
                                <i className="fas fa-unlock-alt icon1 d3"  ></i>
                                <input type="password" class="inp" id="passid" placeholder="Password" />
                                <p className="abc" id="a12">Please fill the input correctly*</p>

                            </div>
                            <div className="mmm">
                                <input type="checkbox" />
                                <label className="rem">Remeber me</label>
                            </div>
                            <input className="btn btn-md btn-success sign" id="signbtn" type="button" value="Sign in" />
                            <br />
                            <label className="lab">Don't have account ? <p class="ap" onClick={() => this.props.history.push("/signup")}> Sign up</p></label>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}
// export default Signin;