import React,{Component} from 'react'
import {
    BrowserRouter as Router,
    Route,
    Link
} from 'react-router-dom'
import {Myroute} from './signin'

export const Sign12=()=>{
    return (
    <Myroute />
        // <h1>askjds</h1>
)
}

const Myroute1=()=>{
    return(
        <Router>
                <div>
                <Route exact path="/signup" component={Signup}/>
                <Route  path="/signin" component={Sign12}/>
                </div>
        </Router>    

)
}
export default Myroute1;

export  class Signup extends Component{
    render(){
        return(
    <div>
        
        <div className="container subDiv">
        <div className="d1"></div>
        <div className="divs">
            <div className="div1 dd1">
                <img src={require("../img/5.png")} className="logo1 dd2" />

            </div>
            <div className="div2 dd3">
                    <h1 className="h1h">Sign Up</h1>
                    <div className="dd4"> 
                            <i className="fas fa-user icon1 d3" ></i>
                            <input type="text" className="inp"  id="userid" placeholder="Full Name"/>
                            <p className="abc" id="a1">Please fill the input correctly*</p>
                    </div>
                    <div className="dd4"> 
                            <i className="fas fa-graduation-cap icon1 d3" ></i>
                            <input type="text" className="inp"  id="eduid" placeholder="Education"/>
                            <p className="abc" id="a2">Please fill the input correctly*</p>                        
                    </div>
                    <div className="dd4"> 
                        <i className="fas fa-envelope-open icon1 d3"></i>
                        <input type="email" className="inp"  id="emailid" placeholder="Email"/>
                        <p className="abc" id="a3">Please fill the input correctly*</p>
                        
                    </div>
                    <div className="dd4">
                        <i className="fas fa-unlock-alt icon1 d3"></i>
                        <input type="password" className="inp"  id="passid" placeholder="Password"/>
                        <p className="abc" id="a4"> Please fill the input correctly*</p>

                    </div>
                    <input type="button" class="btn btn-md btn-success sign"  id="signupbtn" value="Sign up"/>
                   <br/>
                    <label className="lab">I am already a member ! 
                     <p class="ap" onClick={() => this.props.history.push("/signin")}> Sign in</p>
                    
                    </label>
                </div>
        </div>
        </div>
     </div>  
        )
    }
}